
.bg{
    width: 100%;
    height: 75vh;
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    background-image:url("/public/images/edited.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;

    background-position: center;
}
.homee{
    width:30%;
    height:50%;
    display: flex;
    flex-wrap:wrap;
    color: white;
    justify-content: center;
    background-color: rgba(144, 119, 142, 0.5);
    align-items: center;
    font-family:cursive;
    text-align: center;
}
.homee h1{
    margin:0px 20px;
}
.aboutbtn,.contactbtn{
    background-color: transparent;
    font-size: 20px;
    border:2px solid rgb(244, 236, 236);
    color: rgb(244, 236, 236);
    padding: 10px 5px;
    margin:0px 10px;
}
:hover.aboutbtn{
    background-color: white;
    color: black;
}
:hover.contactbtn{
    background-color: white;
    color: black;
} 
@media only screen and (max-width:1305px){
    .homee h1{
        font-size: 25px;
    }
}
@media only screen and (max-width:1049px){
    .homee h1{
        font-size: 22px;
    }
}
@media only screen and (max-width:939px){
    .homee h1{
        font-size: 20px;
    }
}
@media only screen and (max-width:865px){
    .homee h1{
        font-size: 17px;
    }
}
@media only screen and (max-width:461px){
    .aboutbtn,.contactbtn{
        font-size: 15px;
    }
}
@media only screen and (max-width:373px){
    .aboutbtn,.contactbtn{
        font-size: 12px;
    }
}