
.aboutele{
    width:100%;
    height:75vh;
    background-color: rgb(204, 176, 199);
    display: flex;
    justify-content: space-evenly;
   align-items: center;
   text-align: center;
}
.aboutele h2{
    margin-bottom:0px;
}
.aboutele p{
    margin: 0px ;
}
.ele1,.ele2,.ele3{
    width:30%;
    height:60%;
    background-color: rgb(188, 133, 180);
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 20px 0px;
}
.expr,.expr1,.expr2{
    width:80%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.i1,.i2,.i3,.i4{
    width:60%;
    height:30px;
    border: 1px solid rgb(82, 82, 82);
    border-radius:5px ;
    margin:10px 0px;
}
.i1{
    background: linear-gradient(to right,rgb(183, 86, 172) 95%,rgb(241, 247, 247) 5%);
}
.i2{
    background: linear-gradient(to right, rgb(183, 86, 172) 80%,rgb(241, 247, 247) 20%);
}
.i3{
    background: linear-gradient(to right, rgb(183, 86, 172) 75%,rgb(241, 247, 247) 25%);
}
.i4{
    background: linear-gradient(to right, rgb(183, 86, 172) 80%,rgb(241, 247, 247) 20%);
}
.i5,.i6,.i7{
    width:60%;
    height:30px;
    border: 1px solid rgb(82, 82, 82);
    border-radius:5px ;
    margin:5px 0px;
}
.i5{
    background: linear-gradient(to right,rgb(183, 86, 172) 94%,rgb(241, 247, 247) 6%);
}
.i6{
    background: linear-gradient(to right, rgb(183, 86, 172) 77.62%,rgb(241, 247, 247) 23.38%);
}
.i7{
    background: linear-gradient(to right, rgb(183, 86, 172) 75.7%,rgb(241, 247, 247) 24.3%);
} 
@media only screen and (max-width:1084px){
    /* .ele1,.ele2,.ele3{
        padding: 0px 10px 30px 10px;
    }
}
@media only screen and (max-width:945px){ */
    .aboutele{
        display: block;
        height:auto;
    }
    .aboutele h2{
        margin:5px 0px;
    }
    .aboutele p{
        margin:10px 0px ;
    }
    .ele1,.ele2,.ele3{
        width:60%;
      height:auto;
      margin:auto;
      margin-top: 10px;
      margin-bottom: 10px;
    }
    .expr,.expr1,.expr2{
        height:auto;
    }
 }
 @media only screen and (max-width:435px){
    .i1,.i2,.i3,.i4,.i5,.i6,.i7{
        font-size: 12px;
    }
}
@media only screen and (max-width:305px){
    .i1,.i2,.i3,.i4,.i5,.i6,.i7{
        font-size: 10px;
    }
}
@media only screen and (max-width:260px){
    .aboutele p{
        font-size:12px;
    }
}
@media only screen and (max-width:255px){
    .i1,.i2,.i3,.i4,.i5,.i6,.i7{
        width:80%;
    }
}