
.header{
    width:100%;
    height:15vh;
    /* background-color: rgb(188, 133, 180); */
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    font-family:cursive;
    color:white;
}
.hambmenu{
    width:10%;
    display: flex;
    justify-content: center;    
}
.ham{
	width:20px;
	height:1px;
	background-color:black;
	border:2px solid black;
	margin:5px 0px;
}
/* .res{
	float:left;
} */
.hb{
	border:none;
	background-color:white;

}
.hb:hover{
	background-color:rgb(188, 133, 180);
}

.ord{
	display:none;
	width:150px;
	background-color:rgb(204, 176, 199);
	border:1px solid gray;
    text-align: center;
	position:absolute;
	left:20px;
	z-index:1;
}

.cel{
	font-size:18px;
    /* background-color: darkgreen; */
	color:white;
	display:block;
	text-decoration:none;
    padding:5px;
}
.cel:hover{
	border-bottom:5px solid rgb(188, 133, 180);
	color:black;
    background-color:rgb(245, 213, 246) ;
}
.res:hover .ord{
	display:block;
}	
.cel:active{
	color:black;
}
.headercon{
    width:90%;
    height:100%;
    text-align: center;
}
.headercon h2{
    margin-top: 30px;    
}
@media only screen and (max-width:450px){
   .ham{
    width:15px;
   }.headercon h2{
    font-size: 20px;
   }
}
@media only screen and (max-width:360px){
    .headercon h2{
        font-size: 18px;
       }
}
@media only screen and (max-width:255px){
    .headercon h2{
        font-size:16px;
    }
}
@media only screen and (max-width:225px){
    .headercon h2{
        font-size:14px;
    }
}