.contact{
    width:100%;
    height:75vh;
    color: white;
    display: flex;
    justify-content:center;
    align-items: center;
}
.inputbox{
    width:50%;
    background-color: rgb(188, 133, 180);
    color:black;
    text-align: center;
}
.in{
    padding: 5px;
    margin-bottom:15px;
    width:75%;
}
#i1,#i2,#i3,#i4{
    padding-right: 15px;
}
.sendbtn{
    width:100px;
    background-color:rgb(245, 213, 246);
    color:black;
    border: none;
    font-size: 16px;
    margin-bottom:15px;
}
@media only screen and (max-width:470px){
    .inputbox{
        padding-left: 10px;
    }
}
@media only screen and (max-width:450px){
    #con{
        padding: 0px 25px;
    } 
}
@media only screen and (max-width:360px){
    #con i{
        padding: 8px 20px;
    
    }
    .inputbox h2{
        margin-top: 0px;
    }
    .inputbox h3{
        margin-bottom: 0px;
    }
}